label {
  color: #481111;
  font-size: 20px;
  font-weight: 600;
}

form {
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  padding: 2em;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, .1);
}

input {
  width: 15%;
  background-color: #f8f8f8;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin: 5px 0 20px;
  padding: 10px;
  font-size: 16px;
}

input:focus, input:hover {
  border-color: #007bff;
  box-shadow: 0 0 5px rgba(0, 123, 255, .5);
}

button {
  cursor: pointer;
  color: #f0f8ff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin: 25px;
  padding: 10px 20px;
  font-size: 16px;
  transition: all .3s;
}

button:hover {
  background-color: #0953a1;
  box-shadow: 0 0 3px #4a4949;
}

